<template>
	<div id="assign-queue">
		<public-header title="添加队列"></public-header>
		<div class="content">
			<van-cell-group>
				<van-field
						v-model="data.title"
						label="队列名称"
						placeholder="请填写队列名称"
						input-align="right"
				/>
				<van-field
						v-model="data.guest_num"
						label="排入队列"
						placeholder="客人数量少于多少人"
						input-align="right"
				/>
			</van-cell-group>
			<van-cell-group class="margin-10-t">
				<van-cell class="flex-lr" title="排队时间">
					<div slot="right-icon" class="flex">
						<span @click="onToggleTimePicker('s')">{{data.starttime}}</span>
						<span class="padding-10-lr">至</span>
						<span @click="onToggleTimePicker('e')">{{data.endtime}}</span>
					</div>
				</van-cell>
			</van-cell-group>
			<van-cell-group class="margin-10-t">
				<van-field
						v-model="data.prefix"
						label="队列前缀"
						placeholder="请填写编号前缀"
						input-align="right"
				/>
				<van-field
						v-model="data.notify_num"
						label="提前通知"
						placeholder="请填写提前通知人数"
						input-align="right"
				/>
			</van-cell-group>
			<div class="assign-but">
				<van-button size="normal" block class="bg-info font-16" @click="onSubmit">点击添加</van-button>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
		<!--时间选择器-->
		<van-popup v-model="timePickerShow" position="bottom">
			<van-datetime-picker
					v-model="currentDate"
					type="time"
					:min-hour="0"
					:max-hour="23"
					@confirm="onConfirmTime"
					@cancel="onToggleTimePicker"
			/>
		</van-popup>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {setData} from "@/controller/funcCommon"
	export default {
		components: {
			publicHeader,
		},
		data() {
			return {
				timePickerShow: false,
				currentDate: '',
				TimeType: '',
				data: {
					title: '',
					guest_num: '',
					starttime: '0:00',
					endtime: '0:00',
					prefix: '',
					notify_num: ''
				},
				showPreLoading: true
			}
		},
		methods: {
			onToggleTimePicker(type){
				this.timePickerShow = !this.timePickerShow;
				if (type == 's' || type == 'e'){
					this.TimeType = type;
				}
			},
			onConfirmTime(value){
				if (value){
					if(this.TimeType == 's'){
						this.data.starttime = value;
					} else {
						this.data.endtime = value;
					}
				}
				this.onToggleTimePicker();
			},
			onSubmit(){
				var that = this;
				if (!that.data.title) {
					that.util.$toast('请输入队列名称', '', 1000);
					return false;
				}
				if (!that.data.guest_num) {
					that.util.$toast('客人数量少于多少人必须大于0', '', 1000);
					return false;
				}
				if (!that.data.notify_num) {
					that.util.$toast('提前通知人数必须大于0', '', 1000);
					return false;
				}
				setData({
					vue: this,
					url: 'manage/order/assign/queue_post',
					data: that.data,
					redirect: that.util.getUrl({path: '/pages/order/assign'}),
					message: '添加队列成功'
				});
			}
		},
		mounted(){
			this.showPreLoading = false;
		}
	}
</script>

<style>
	#assign-queue .content .assign-but{
		position:fixed;
		bottom:0;
		left:0;
		background-color:#fff;
		width:100%;
		padding: 10px;
	}
</style>


